// 拿到vuex中的函数
import {mapGetters, mapMutations} from 'vuex'
import Toast from '../../wxcomponents/weapp/dist/toast/toast';
// 设置用户信息
import {http_setUserInfoData} from '../../utils/http/http_setUserInfoData.js'
export default {
	data() {
		return {
			addrs: ''
		}
	},
	onLoad() {
		
		// 获取用户地址信息
		// 没有值的话是等于null，这里转化一下，如果是null那么就让 this.addrs = ''
		if( this.store_UserInfoData.address===null) {
			this.addrs = ''
		} else {
			this.addrs = this.store_UserInfoData.address
		}
		
	},
	methods: {
		// 点击提交按钮
		async subAddrs() {
			let res = this.addrs.trim()
			// 判断输入框内容长度
			if(!res.length) {
				Toast('请输入内容')
				this.addrs = ''
			} else if(this.addrs!==this.store_UserInfoData.address){ 
				// 判断是否等于原来的内容
				console.log('当前内容 => ' + res)
				let httpRes = await http_setUserInfoData({"address": res})
				
				if(httpRes) {
					// 提交成功
					Toast('已保存')
					// 此时发送请求，如果返回成功的话，那么在http请求中手动设置本地store，那么这里就能获取到最新的地址的值
					this.addrs = this.store_UserInfoData.address
					setTimeout(()=>{
						uni.navigateBack({
						    delta: 2
						})
					},1000)
				} else {
					Toast('修改失败')
				}
				
			} else {
				Toast('地址未修改')
			}
		},
	},
	// watch: {
	// 	addrs(newVal) {
	// 		console.log(newVal)
	// 	}
	// },
	computed: {
		...mapGetters([
			'store_UserInfoData'
		])
	}
}
